<template>
<aside class="aside-container">
    <!-- START Sidebar (left)-->
    <div class="aside-inner">
        <nav class="sidebar" data-sidebar-anyclick-close="">
            <!-- START sidebar nav-->
            <ul class="sidebar-nav">
                <li class="nav-item">
                    <router-link class="nav-link" to="/admin/products">
                    <i data-feather="flag"></i>
                    產品列表 <span class="sr-only">(current)</span>
                    </router-link>
                </li>
            </ul>
            <ul class="sidebar-nav">
                <li class="nav-item">
                    <router-link class="nav-link" to="/admin/control">
                    <span data-feather="folder"></span>
                    控制台
                    </router-link>
                </li>
                <li class="nav-item">
                    <router-link class="nav-link" to="/admin/service">
                    <span data-feather="cloud"></span>
                    服務治理
                    </router-link>
                </li>
                <li class="nav-item">
                    <router-link class="nav-link" to="/admin/tabless">
                    <span data-feather="settings"></span>
                    配置項
                    </router-link>
                </li>
                <li class="nav-item">
                    <a class="nav-link" href="#">
                    <span data-feather="layers"></span>
                    鏡像倉庫
                    </a>
                </li>
                <li class="nav-item">
                    <a class="nav-link" href="#">
                    <span data-feather="bar-chart-2"></span>
                    雲硬碟數據捲
                    </a>
                </li>
            </ul>
        </nav>
    </div>
</aside>        
</template> 

<script>
import feather from 'feather-icons'
export default {
    mounted(){
        feather.replace()
    }
}
</script>

<style>
.feather {
    width: 16px;
    height: 16px;
    vertical-align: text-bottom;
}
.sidebar .nav-link .feather {
    margin-right: 4px;
    color: #999;
}
</style>

       



