<template>
    <div class="block-center mt-4 wd-xl">
        <!-- START card-->
        <div class="card card-flat">
            <div class="card-header text-center bg-dark">
                <a href="#">
                </a>
            </div>
            <div class="card-body">
                <p class="text-center py-2">請先登入</p>
                <form class="mb-3"  @submit.prevent="signin">
                    <div class="form-group">
                        <div class="input-group with-focus">
                            <input :class="{'form-control border-right-0':true}" placeholder="請輸入帳號" v-model="user.username" type="text" name="email"/>
                            <div class="input-group-append">
                                <span class="input-group-text text  -muted bg-transparent border-left-0">
                                    <em class="fa fa-envelope"></em>
                                </span>
                            </div>
                        </div>
                    </div>
                    <div class="form-group">
                        <div class="input-group with-focus">
                            <input :class="{'form-control  border-right-0':true}" v-model="user.password"  type="password" name="password" placeholder="請輸入密碼"/>
                            <div class="input-group-append">
                                <span class="input-group-text text-muted bg-transparent border-left-0">
                                    <em class="fa fa-lock"></em>
                                </span>
                            </div>
                        </div>
                    </div>
                    <div class="clearfix">
                        <div class="float-left">
                            <div class="custom-control custom-checkbox">
                                <small>預設帳號:test123@gmail.com</small><br>
                                <small>預設密碼:1234567</small>
                            </div>
                        </div>
                    </div>
                    <button class="btn btn-block btn-primary mt-3" type="submit">登入</button>
                </form>
            </div>
        </div>
    </div>
</template>
<script>
  
    export default {
        name: 'App',
        data() {
            return {
  
                user:{
                    username:'',
                    password:''
                }
            }
        },
        methods: {
            signin(){
                const api ='https://vue-course-api.hexschool.io/api/frankguoo/products';
                const vm = this;
                this.$http.get(api).then((response) => {
                  response.data.products.forEach(function(item,key){
                    if(response.data.products[key].title + '@gmail.com' == vm.user.username && response.data.products[key].unit == vm.user.password){
                        sessionStorage.setItem('token', 'ImLogin')
                        vm.$router.push('/admin/products');
                        vm.title = response.data.products[key].id
                        console.log(vm.title);
                    }
                  });
                });
            }
            // validateBeforeSubmit(scope) {
            //     this.$validator.validateAll(scope).then((result) => {
            //         if (result) {
            //             console.log('Form Submitted!');
            //             console.log(`Email: ${this.login.email}`)
            //             console.log(`Password: ${this.login.password}`)
            //             console.log(`Remember Me: ${this.login.rememberme}`)
            //             return;
            //         }
            //         console.log('Correct them errors!');
            //     });
            // }
        }
    }
</script>



