import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/pages/login'
import Dashboard from '@/components/dashboard'
import Service from '@/components/service'
import Control from '@/components/control'
import Table1 from '@/components/table1'
import Table2 from '@/components/table2'



Vue.use(Router)
export default new Router({
  routes: [
	{
		path: '/admin',
		name: 'Dashboard',	
		component: Dashboard,
		meta: { 
			requiresAuth: true 
		},
		children:[
			{
				path: 'control',
				name: 'control',
				component: Control,
				meta: { 
					requiresAuth: true
				},
				children:[
					{
						path: 'table1',
						name: 'table1',
						component: Table1,
						meta: { 
							requiresAuth: true
						},
					},
					{
						path: 'table2',
						name: 'table2',
						component: Table2,
						meta: { 
							requiresAuth: true
						},
					},
				]	
			},
			{
				path: 'service',
				name: 'service',
				component: Service,
				meta: { 
					requiresAuth: false
				},
			}
		],
	},
    {
		path: '/login',
		name: 'login',
		component: Login
	},
	{
		path: '/',
		component: Login
	},
  ]
})



